import { createSlice } from "@reduxjs/toolkit";

const postsReducers = createSlice( {
    name : 'posts',
    initialState : {items : []},
    reducers : {
     addPost : ( state , action)=>{
        state.items.push(action.payload)
     },
     deletePost : ( state, action)=>{
       state.items = state.items.filter(item => item.id != action.payload)
     },
     EditPost : ( state , action )=>{
        state.items.map( item => {
        if( item.id == action.payload.id ){
          item.postName = action.payload.postName
          item.postDescription = action.payload.postDescription
        }
        })
     }
    }
})

export const {addPost , deletePost ,EditPost} =  postsReducers.actions
export  default postsReducers.reducer  