import React, { useCallback, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { addPost , deletePost , EditPost} from '../Redux/PostsSlice'
import './Post.css'
export const Post = () => {
  const [changebutton , setChangeButton ] = useState(true)
  const [postName , setPostname ] = useState('')
  const [postDescription , setPostDecription ] = useState('')
  const [postId , setPostId ] = useState('')

  const posts = useSelector( state => state.posts.items)
  const dispatch = useDispatch()
  
  const handleAdd = ()=>{
    dispatch(addPost({ id: posts.length+1 , postName , postDescription}))
    setPostname('')
    setPostDecription('')
  }
 const handleEdit = (e)=>{ 
   setChangeButton(false)
   setPostname(e.postName )
   setPostDecription(e.postDescription)
   setPostId(e.id)
 }

 const HandleUpdate = ()=>{
    setChangeButton(true)
    dispatch(EditPost({ id: postId ,  postName   ,  postDescription }))
    setPostname('')
    setPostDecription('')
 }
  return (
    <>
    <div className='form'>
        <div className='container'>
            <h2> CRUD Application</h2>
            <div className='addPost'>
            <div className='form-group'>
               <input type='text' className='form-control' placeholder='Post Title...' value={postName} onChange={ (e)=>{setPostname(e.target.value)}}/>
            </div>
            <div className='form-group'>
               <input type='text' className='form-control' placeholder='Post Description..' value={postDescription} onChange={ (e)=>{setPostDecription(e.target.value)}}/>
            </div>
            <div className='form-btn '> 
            {changebutton ? <button className='btn btn-info' onClick={handleAdd}>Add Post</button> : <button className='btn btn-warning' onClick= { HandleUpdate}>update Post</button> }
               
            </div>
            </div>
          
          {posts.length > 0 ? posts.map ((post , i)=> 
            <div className='post-content py-5 border  shadow' key={i}>
             <div className='postBr '>
                <h5>{post.postName}</h5>
                <p className='text-mute'>{post.postDescription}</p>
                <div className='postBtns'>
                  <button className='btn btn-warning'onClick={()=>handleEdit(post)}>Edit</button>
                  <button className='btn btn-danger'onClick={()=>dispatch(deletePost( post.id )) }>Delete</button>
                </div>
             </div>
         </div>
            
            ) : <div className='text-center py-4 text-secondary'>There is no posts!</div>}
         
            
        </div>
    </div>
    </>
  )
}
